<template>
     <div>
         <basic-header :items="['订单管理','订单查看']"></basic-header>
         <el-card style="margin-top: 30px">
             <el-tabs v-model="activeName">
                 <el-tab-pane label="已支付" name="first">
                     <el-table :data="tableData1">
                         <el-table-column prop="id" label="ID" align="center"></el-table-column>
                         <el-table-column prop="order_id" label="订单号" align="center"></el-table-column>
                         <el-table-column prop="nickName" label="用户昵称" align="center"></el-table-column>
                         <el-table-column label="配送地址" align="center">
                             <template #default="o">
                                 {{o.row.province+o.row.city+o.row.area+o.row.detail}}
                             </template>
                         </el-table-column>
                         <el-table-column prop="total" label="订单总价" align="center"></el-table-column>
                         <el-table-column prop="backup" label="订单备注" align="center"></el-table-column>
                         <el-table-column prop="create_time" label="创建时间" align="center"></el-table-column>
                         <el-table-column prop="name" label="收货人" align="center"></el-table-column>
                         <el-table-column prop="phone" label="联系电话" align="center"></el-table-column>
                         <el-table-column prop="operating" label="操作" align="center">
                             <template #default="o">
                                 <el-button type="text" @click="">查看详情</el-button>
                                 <el-button type="text" @click="">发货</el-button>
                             </template>
                         </el-table-column>
                     </el-table>
                 </el-tab-pane>
                 <el-tab-pane label="已发货" name="second"></el-tab-pane>
                 <el-tab-pane label="已完成" name="third"></el-tab-pane>
             </el-tabs>
         </el-card>
     </div>
</template>

<script>
    import MBXhead from "../../components/MBXhead";
    export default {
        data() {
            return {
                activeName:"first",
                tableData1:[],
                page:1,
                pageSize:5,
                total:0,
                api:"/api/orders/orders",
                state:1
            }
        },
        methods: {
            fetchData(){
                this.axios.get(this.api,{
                    params:{
                        state:this.state,
                        page:this.page,
                        pageSize:this.pageSize
                    }
                }).then(res=>{
                    if(res.data.code===200){
                        this.tableData1=res.data.data;
                        this.total=res.data.total;
                    }
                })
            }
        },
        components:{
           "basic-header":MBXhead
        },
        created() {

        },
        mounted() {
            this.fetchData()
        },
    }
</script>
<style scoped>

</style>